export const matchAge = (is_adult: boolean, id: string) => {
    let res = '';
    switch (is_adult) {
        case true:
            res = '';
            break;

        case false:
            res = '';
            break;

        default:
            break;
    }

    if (id.includes('childform')) {
        res = ' ( )';
    }

    return res;
};
