export const mapDocnumber = (doctype: string) => {
    const birthReg = '^[I,V,X,L,C,i,v,x,l,c]{1,4}[--]{2}\\d{6}$';
    const passportReg = '^\\d{6}$';

    switch (doctype) {
        case '  ':
            return {
                reg: birthReg,
                symbols: 12,
            };
        default:
            return {
                reg: passportReg,
                symbols: 6,
            };
    }
};
