const fillReviews = () => {
    const reviewsData = [
        {
            src: './img/reviews/Kate.png',
            title: '1',
            desc: '               / ,       .',
        },
        {
            src: './img/reviews/Eugene.png',
            title: '2',
            desc: '-      /    3       -  .',
        },
        {
            src: './img/reviews/Eugene.png',
            title: '3',
            desc: '-      /    3       -  .',
        },
        {
            src: './img/reviews/Kate.png',
            title: '4',
            desc: '               / ,       .',
        },
        {
            src: './img/reviews/Kate.png',
            title: '5',
            desc: '               / ,       .',
        },
    ];

    let offset = 0;

    while (reviewsData.length % 2 > 0) {
        reviewsData.push(reviewsData[offset]);
        offset += 1;
    }

    return reviewsData;
};

export const reviewsData = fillReviews();
