export const mapExtra = (key: string) => {
    switch (key) {
        case 'wifi_price':
            return 'Wi-Fi';

        case 'linens_price':
            return ' ';

        case 'conditioner':
            return '';

        default:
            return '';
    }
};

export const mapPlaceValue = (key: string) => {
    switch (key) {
        case 'luxe':
            return '';

        case 'seatable':
            return '';

        case 'top':
            return '';

        case 'bottom':
            return '';

        case 'side':
            return '';

        default:
            return '';
    }
};
